import React, { useState } from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Checkbox from '@material-ui/core/Checkbox';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Snackbar from '@material-ui/core/Snackbar';
import axios from 'axios';


const SignUpForm = ({ userData, updateSuccess }) => {
  const [open, setOpen] = React.useState(false);
  const [userName, setUserName] = useState(userData.username);
  const [password, setPassword] = useState(userData.password);
  const [verified, setVerified] = useState(userData.verified);
  const [active, setActive] = useState(userData.active);
  const [superAdmin, setSuperAdmin] = useState(userData.superAdmin);

  let calls:any = [];
  let userSaved = false;
  let userSaveError = false;
  
  const patchData = (url, data) => {
      return axios.patch(url, data)
      .then((d)=>{
        
      })
      .catch((error) => {
        // handle error
        console.error(error);
        userSaved = true;
      })
      .then(() => {
        // always executed
        userSaveError = true;
      });
  }
  
  const submitForm = () => {
    const url = "/api/signup";
    axios.post(url, {
      "username":userName,
      "password":password
    })
    .then((d:any)=>{
      let uuid = d.uuid;
      calls.push(patchData("/api/superadmin/users/"+uuid+"/verified", {"verified":verified}));
      calls.push(patchData("/api/superadmin/users/"+uuid+"/active", {"active":active}));
      calls.push(patchData("/api/superadmin/users/"+uuid+"/roles", {"roles":(superAdmin ? "SUPERADMIN" : "USER")}));
      axios.all([...calls]).then(axios.spread((...responses) => {
        calls = [];
        userSaved = true;
        responses.forEach((item,index)=>{
          
        });
        updateSuccess();
      })).catch(errors => {
        calls = [];
        setOpen(true);
        userSaveError = true;
      });
    })
    .catch((error) => {
      console.error(error);
    })
    .then(() => {
    });
  }

  return (
    <form onSubmit={e => { e.preventDefault();submitForm(); }}>
      <h2 className="mb-5">Add a new user</h2>
      <div className="row">
        <div className="col-md-6">
          <div className="form-group">
            <TextField required value={userName} className="formField" onChange={e => setUserName(e.target.value)} label="User name" variant="outlined"></TextField>
          </div>
          <div className="form-group">
            <TextField required value={password} className="formField" onChange={e => setPassword(e.target.value)} label="Password" type="password" variant="outlined"></TextField>
          </div>
        </div>
        <div className="col-md-6">
          <div className="form-group">
            <FormControlLabel
              control={<Checkbox color="primary" checked={superAdmin} onChange={e => setSuperAdmin(!superAdmin)}></Checkbox>}
              label="Superadmin"
            ></FormControlLabel>
          </div>
          <div className="form-group">
            <FormControlLabel
              control={<Checkbox color="primary" checked={verified} onChange={e => setVerified(!verified)}></Checkbox>}
              label="Verified"
            ></FormControlLabel>
          </div>
          <div className="form-group">
              <FormControlLabel
              control={<Checkbox color="primary" checked={active} onChange={e => setActive(!active)}></Checkbox>}
              label="Active"
            ></FormControlLabel>
          </div>
        </div>
      </div>
      <div className="mt-3">
        <Button className="long-button" variant="contained" color="primary" type="submit">Save</Button>
      </div>

      <Snackbar open={open} autoHideDuration={6000}>
        <div className="alert alert-danger">User save error</div>
      </Snackbar>
    </form>
  )
}

export default SignUpForm;