import React from 'react';
import './Users.scss';
import { createStyles, withStyles, WithStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Breadcrumbs from '@material-ui/core/Breadcrumbs';
import Link from '@material-ui/core/Link';
import Typography from '@material-ui/core/Typography';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import AddIcon from '@material-ui/icons/Add';
import UserForm from './userForm';
import SignUpForm from './signUpForm';
import utils from '../../services/utils.js';
import Spinner from '../../sharedComponents/functionalComponents/spinner/Spinner';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTrashAlt, faEdit, faWindowClose, faCheck } from '@fortawesome/free-solid-svg-icons'

import axios from 'axios';

const defaultSuperadmin = "superadminuser@artfx.io";

const modalStyles = () => createStyles({
  modal: {
  }
});

interface State {
  rows:any,
  showUserEditModal:boolean,
  userBeingEdited:any,
  showSignUpForm:boolean
}

interface Props extends WithStyles<typeof modalStyles>{ }

class UsersPage extends React.Component {
  public state:State;
  private cancelTokenSource:any;
  private actions = [{
    "action":"delete"
  },{
    "action":"edit"
  }];
  
  constructor(props) {
    super(props);
    this.state = {
      rows:[],
      showUserEditModal:false,
      userBeingEdited:{},
      showSignUpForm:false
    };
  }
  
  componentDidMount() {
    console.log(localStorage.getItem("authToken"))
    if(!localStorage.getItem("authToken"))
      window.location.href = "/";
      
    this.loadUsers();
  }
  
  loadUsers() {
    const url = "/api/superadmin/users";
    if(this.cancelTokenSource)
      this.cancelTokenSource.cancel();
    this.cancelTokenSource = axios.CancelToken.source();

    axios.get(url, {
      cancelToken: this.cancelTokenSource.token
    })
    .then((d)=>{
      console.log("#######");
      console.log(d);
      let users = d.data;
      let rows:any = [];
      users.forEach((item, index)=>{
        console.log(item);
        rows.push(this.createData(item.uuid, item.username, item.verified, item.active, item.superAdmin, item.roles, [...this.actions]));
      });
      
      this.setState({rows:rows});
    })
    .catch((error) => {
      console.error(error);
    })
    .then(() => {
    });
  }

  createData(uuid, username, verified, active, superAdmin, roles, actions) {
    return { uuid, username, verified, active, superAdmin, roles, actions };
  }
  
  deleteUser(user) {
    if(window.confirm("Delete user "+user.username+"?")) {
      const url = "/api/superadmin/users?username="+user.uuid;
      if(this.cancelTokenSource)
        this.cancelTokenSource.cancel();
      this.cancelTokenSource = axios.CancelToken.source();
  
      axios.delete(url, {
        cancelToken: this.cancelTokenSource.token
      })
      .then((d)=>{
        let token = localStorage.getItem("authToken");
        let parsed = utils.parseJwt(token);
        if(parsed.sub === user.username) {
          utils.logout();
        }
        else {
          this.loadUsers();
        }
      })
      .catch((error) => {
        console.error(error);
      })
      .then(() => {
      });
    }
  }
  
  editUser(user) {
    this.setState({userBeingEdited:{...user},showUserEditModal:true});
  }
  
  handleUserEditModalClose() {
    this.setState({userBeingEdited:{},showUserEditModal:false});
  }
  
  userUpdateSucces() {
    this.loadUsers();
    this.handleUserEditModalClose();
  }
  
  addUser() {
    this.setState({showSignUpForm:true});
  }
  
  handleSignUpFormModalClose() {
    this.setState({showSignUpForm:false});
  }
  
  userSignUpSucces() {
    this.loadUsers();
    this.handleSignUpFormModalClose();
  }
  
  render() {
    return (
    <section className="usersPage">
      <Breadcrumbs aria-label="breadcrumb" className="mb-3">
        <Link color="inherit" href="/">
          Superadmin
        </Link>
        <Typography color="textPrimary">Manage Users</Typography>
      </Breadcrumbs>
      <div className="tableGrid">
       <div className="row">
        <div className="col-md-12">
          <BottomNavigation value={"add"}>
            <BottomNavigationAction value="add" icon={<AddIcon />} onClick={(e)=>{this.addUser();}} />
          </BottomNavigation>
          <TableContainer component={Paper}>
            <Table className={''} aria-label="simple table">
              <TableHead>
                <TableRow>
                  <TableCell>UUID</TableCell>
                  <TableCell align="left">User name</TableCell>
                  <TableCell align="left">Verified</TableCell>
                  <TableCell align="left">Active</TableCell>
                  <TableCell align="left">Superadmin</TableCell>
                  <TableCell align="left">Roles</TableCell>
                  <TableCell align="left">Actions</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {this.state.rows.map((row, index) => (
                  <TableRow key={index}>
                    <TableCell component="th" scope="row">
                      {row.uuid}
                    </TableCell>
                    <TableCell align="left">{row.username}</TableCell>
                    <TableCell align="left">{row.verified && <FontAwesomeIcon icon={faCheck} />}</TableCell>
                    <TableCell align="left">{row.active && <FontAwesomeIcon icon={faCheck} />}</TableCell>
                    <TableCell align="left">{row.superAdmin && <FontAwesomeIcon icon={faCheck} />}</TableCell>
                    <TableCell align="left">{row.roles}</TableCell>
                    <TableCell align="left">
                      {row.actions.map((item, index)=>{
                        return <span className={"action-icon px-1 "+item.action} key={index}>
                          {item.action === "delete" && row.username !== defaultSuperadmin && <FontAwesomeIcon onClick={(e)=>{this.deleteUser(row);}}  icon={faTrashAlt} />}
                          {item.action === "edit" && row.username !== defaultSuperadmin && <FontAwesomeIcon onClick={(e)=>{this.editUser(row);}}  icon={faEdit} />}
                        </span>;
                      })}
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
          <Modal
            open={this.state.showUserEditModal}
            onClose={(e)=>this.handleUserEditModalClose()}
            closeAfterTransition
            className="modalStyles"
            BackdropComponent={Backdrop}
            disableBackdropClick
            BackdropProps={{
              timeout: 500,
            }}
          >
            <div className="modalLiner">
              <FontAwesomeIcon className="closeButton" onClick={(e)=>{this.handleUserEditModalClose();}} icon={faWindowClose} />
              <UserForm userData={this.state.userBeingEdited} updateSuccess={this.userUpdateSucces.bind(this)}/>
            </div>
          </Modal>
          <Modal
            open={this.state.showSignUpForm}
            onClose={(e)=>this.handleSignUpFormModalClose()}
            closeAfterTransition
            className="modalStyles"
            BackdropComponent={Backdrop}
            disableBackdropClick
            BackdropProps={{
              timeout: 500,
            }}
          >
            <div className="modalLiner">
              <FontAwesomeIcon className="closeButton" onClick={(e)=>{this.handleSignUpFormModalClose();}} icon={faWindowClose} />
              <SignUpForm userData={{}} updateSuccess={this.userSignUpSucces.bind(this)}/>
            </div>
          </Modal>
        </div>
       </div>
      </div>
    </section>
    );
  }
}

export default withStyles(modalStyles)(UsersPage);