import React, { useState } from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Checkbox from '@material-ui/core/Checkbox';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Snackbar from '@material-ui/core/Snackbar';
import axios from 'axios';


const UserForm = ({ userData, updateSuccess }) => {
  const [open, setOpen] = React.useState(false);
  const [userName, setUserName] = useState(userData.username);
  const [verified, setVerified] = useState(userData.verified);
  const [active, setActive] = useState(userData.active);
  const [superAdmin, setSuperAdmin] = useState(userData.superAdmin);
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [phone, setPhone] = useState("");
  
  const uuid = userData.uuid;
  
  let userUpdated = false;
  let userUpdatedError = false;
  let calls:any = [];
  
  const patchData = (url, data) => {
      return axios.patch(url, data)
      .then((d)=>{
        
      })
      .catch((error) => {
        // handle error
        console.error(error);
        userUpdated = true;
      })
      .then(() => {
        // always executed
        userUpdatedError = true;
      });
  }
  
  const submitForm = () => {
    if(calls.length === 0) {
      userUpdated = false;
      userUpdatedError = false;
      
      if(verified !== "" && verified !== userData.verified) {
        calls.push(patchData("/api/superadmin/users/"+uuid+"/verified", {"verified":verified}));
      }
      if(active !== "" && active !== userData.active) {
        calls.push(patchData("/api/superadmin/users/"+uuid+"/active", {"active":active}));
      }
      if(superAdmin !== "" && superAdmin !== userData.superAdmin) {
        calls.push(patchData("/api/superadmin/users/"+uuid+"/roles", {"roles":(superAdmin ? "SUPERADMIN" : "USER")}));
      }
      axios.all([...calls]).then(axios.spread((...responses) => {
        calls = [];
        userUpdated = true;
        responses.forEach((item,index)=>{
          
        });
        updateSuccess();
      })).catch(errors => {
        calls = [];
        setOpen(true);
        userUpdatedError = true;
      });
    }
  }

  return (
    <form onSubmit={e => { e.preventDefault();submitForm(); }}>
      <h2 className="mb-5">Edit user details</h2>
      <div className="row">
        <div className="col-md-6">
          <div className="form-group">
            <TextField disabled value={userName} className="formField" onChange={e => setUserName(e.target.value)} label="User name" variant="outlined"></TextField>
          </div>
          <div className="form-group">
            <TextField disabled value={firstName} className="formField" onChange={e => setFirstName(e.target.value)} label="First name" variant="outlined"></TextField>
          </div>
          <div className="form-group">
            <TextField disabled className="formField" onChange={e => setLastName(e.target.value)} label="Last name" variant="outlined"></TextField>
          </div>
          <div className="form-group">
            <TextField disabled className="formField" onChange={e => setPhone(e.target.value)} label="Phone" variant="outlined"></TextField>
          </div>
        </div>
        <div className="col-md-6">
          <div className="form-group">
            <FormControlLabel
              control={<Checkbox color="primary" checked={superAdmin} onChange={e => setSuperAdmin(!superAdmin)}></Checkbox>}
              label="Superadmin"
            ></FormControlLabel>
          </div>
          <div className="form-group">
            <FormControlLabel
              control={<Checkbox color="primary" checked={verified} onChange={e => setVerified(!verified)}></Checkbox>}
              label="Verified"
            ></FormControlLabel>
          </div>
          <div className="form-group">
              <FormControlLabel
              control={<Checkbox color="primary" checked={active} onChange={e => setActive(!active)}></Checkbox>}
              label="Active"
            ></FormControlLabel>
          </div>
        </div>
      </div>
      <div className="mt-3">
        <Button className="long-button" variant="contained" color="primary" type="submit">Save</Button>
      </div>

      <Snackbar open={open} autoHideDuration={6000}>
        <div className="alert alert-danger">User signup error</div>
      </Snackbar>
    </form>
  )
}

export default UserForm;